	module.exports = {"data":{"markdownRemark":{"html":"<p>Tiåret fra 1875 til 1885 var som sagt år med sterke brytninger i Griegs liv. Men selv om han ikke var særlig produktiv, skapte han verk som kom til å bli stående som søyler i opusrekken. Sentralt står to verk, Ballade, op. 24 og Strykekvartett, op. 27. Begge går i g-moll, denne tonearten som Grieg i likhet med mange andre komponister bruker for å uttrykte sine sterkeste følelser.</p>\n<p>Strykekvartetten, op. 27 representerer ikke bare noe av det heftigste i norsk musikkhistorie, men var et banebrytende og enestående verk i europeisk samtidsmusikk. Franz Liszt uttalte etter å hørt den fremført i Wiesbaden, at kvartetten var noe av det mest spennende som var laget i europeisk kammermusikk på 1870-80 tallet.  </p>\n<p>Etter ankomsten til Hardanger begynte arbeidet med strykekvartetten å ta form. Han tilkjennegav overfor vennen Gottfred Matthison-Hansen at  </p>\n<blockquote>\n<p>”.. det neste du får se av meg, blir for strykeinstrumenter; jeg er midt i arbeidet, men Gud vet når det blir slutt. Jeg er just nu i en udugelighetstilstand, som skal gjøres vold på…”  </p>\n</blockquote>\n<p>Og da Grieg, etter å ha fullført kvartetten i 1878, igjen skrev til Matthison-Hansen, var det en Grieg som var helt bevisst hvilket verk han hadde komponert.</p>\n<blockquote>\n<p>”…Den er ikke anlagt på at bringe Smååndrikheter til torvs. Den streber mot bredde, flukt, og framfor alt mot klang for de instrumenter den er skrevet for.”</p>\n</blockquote>\n<p>Grieg la heller ikke skjul på hva som egentlig lå i denne dramatiske musikken. Til biografen Aimar Grønvold skrev han i 1883:  </p>\n<blockquote>\n<p>”…For jeg føler at i dette verk gjemmes prøver på det hjerteblod hvorav fremtiden forhåpentlig vil få se mer enn bare dråper…”</p>\n</blockquote>\n<p>Kvartetten innledes med et dramatisk musikalsk utsagn – et imperativ. Men det følges av  vending gjennom en ”håpløs” akkord til en åpen løsning. Ut fra åpningsmotivet bygges hele komposisjonen opp. Det motiviske slektskap er vevd inn i alle satsene. Dette var ideer som Liszt hadde begynt å anvende, men Grieg var den første som i så konsekvent grad anvendte dette i et større verk. Det skulle gå 15 år før Debussy gjorde noe tilsvarende i sin Strykekvartett i g-moll.</p>\n<p>For å gi musikken det dramatiske uttrykk han ønsket, brukte Grieg en tett, orkestral instrumentering med utstrakt bruk av dobbeltgrep og tett stemmevev. Dette var ganske uvanlig sammenlignet med tradisjonell kammermusikk. Grieg ble kritisert både i sin samtid og fra tid til annen i dag for den orkestrale klang. Kommentarer som ” tjukt og grøtete” hører man ikke sjelden. Grieg avfeide dette med at hvis de spilte etter hans intensjoner, ville den tette stemmeføringen verken tilsløre melodi eller form.  </p>\n<p>Det er interessant å studere det tematiske materiale som ligger til grunn for kvartetten. Da Grieg hadde avsluttet arbeidet med Peer Gynt-musikken, var han så inspirert av Ibsens dikteriske kraft at han også satte musikk til 6 dikt, op. 25. En av sangene, Spillemenn, handler om konflikten mellom kjærlighetens og kunstens kall. Diktet åpner med:</p>\n<h2>Til henne stod mine tanker</h2>\n<p>hver en månelys nat;<br>\nmen vejen den bar til elven,<br>\nved det duggede orekrat.<br>\nHej! Kjender du gru og sange,<br>\nkan du kogle den dejliges sind,<br>\nså i store kirker og sale<br>\nhun mener at følge dig ind</p>\n<p>Men kampen for å nå kunstens innerste vesen har sin pris, og dikteren konkluderer med:</p>\n<p>Jeg maned den våde af dybet;<br>\nhan spilled mig bent fra Gud -;<br>\nmen da jeg var bleven hans mester,<br>\nvar hun min broders brud.<br>\nI store kirker og sale<br>\nmig selv jeg spilled ind,<br>\nog fossens gru og sange<br>\nveg aldrig fra mit sind.</p>\n<p>Diktet gir kanskje nøkkelen til å forstå hvorfor Grieg brukte denne sangen som grunnstoff i kvartetten. Hele hans krisefylte livssituasjon kunne leses ut av teksten, og det var dette han maktet å uttrykke gjennom strykekvartetten. Av Griegs skissebøker kan vi se at han hadde problemer med å komme i gang med kvartetten. Det var først da han valgte Spillemann-motivet som et slags motto – en idé fix – at det løsnet.</p>\n<p>For å understreke det dramatiske i verket, lar han det først klinge moll. Først når mottoet klinger i sidetemaet, er det som i sangen – i dur. Dette mottoet følger oss gjennom hele kvartetten. Til tider er det nesten gjemt bort i stemmeveven - andre ganger utbygd til store melodiske fraser. I siste satsens avslutning ender mottoet opp som et bredt anlagt tema der noteverdiene er strukket ut. I et kaosaktig brus av stemmer seiler temaet gjennom bråttsjøene fram mot noe som kan bli den endelige løsning.</p>\n<p>Men nei, verket avsluttes på samme måte som det åpnet. Det er ingen ende på kampen. Grieg setter bare en kraftig sluttstrek for musikken gjennom et utbrudd i G-dur, før det hele munner ut i en unison, lang g….  </p>\n<p>Griegs strykekvartett er hans erkjennelse og bearbeidelse av de store dilemmaer. Stoffet har inneholdt for mange konflikter og opprivende følelser til å kunne konkluderes. De vil – som musikken – leve i oss i ettertid.  </p>\n<p>Mellom de dramatiske yttersatsene belyser Grieg disse konfliktene på ulik måte i 2. og 3. sats. I 2. sats Romanze lar han to helt forskjellige temaer støte sammen. Celloen innleder med en vakker, lyrisk melodi. Denne brytes av et frenetisk midtparti der solostemmen, 1. fiolin, farer rundt og rundt, hakker og spretter. Alt dette over et akkompagnement som virrer fra toneart til toneart. Inne i dette kaotiske vevet finner vi fragmenter av mottoet. Satsen avrundes med en variant av første delen. Resignert ro får det siste ord i denne krangelen.</p>\n<p>Den langsomme satsen, som nesten ble til et intermesso, blir etterfulgt av et ordentlig Intermezzo. Også denne satsen er i hovedsak bygget opp som svært mye av Griegs musikk – i ABA-form. Første del er høydramatisk. Her bruker Grieg mottoet i en annen rytmisk form. Sterk synkopering (2 mot 3) gir følelsen av steilhet og motstand. Så bryter andre del ut med en feiende, men sidrompa halling i celloen. De andre instrumentene følger etter med lystige kast. A-delen kommer igjen, før Grieg i dette Intermezzo kaster et lite glitter av humør i avrundingen/codaen.</p>\n<p>I sluttføringen av arbeidet finner vi at Grieg hadde utstrakt kontakt med fiolinisten Robert Heckmann i Tyskland. Diskusjonen viser at Grieg i mange ting fulgte Heckmanns råd, mens andre ganger har han skrevet et tydelig nei! ved forslaget.</p>\n<p>Den 29. oktober 1878 ble kvartetten urfremført av Heckmannkvartetten i Köln. Mottagelsen fra publikum var meget positiv, mens enkelte kritikere stilte seg helt uforstående til musikkens ide og massive klangbilde. Kvartetten har imidlertid etablert seg som et sentralt verk i litteraturen.</p>\n<p>Et annet særtrekk ved kvartetten er Griegs tykke klangbilde. Han bruker dobbeltgrep og fortissimo i flere instrumenter samtidig. Dette har fått en del utøvere og publikummere til å stille seg kritisk til verket. Men Grieg ønsket dette klangbilde. Kvartetten var ikke anlagt ”..for å bringe små-åndrikheter til torgs. Den streber mot bredde, flukt, og fremfor alt mot klang for de instrumenter for hvilke den er skrevet.”</p>\n<p>Endelig er kvartetten i stor utstrekning ført i en utpreget homofon stil. Men ikke mer enn at Grieg i polyfone partier -  raffinert og med stor sikkerhet, viser at han også behersker denne teknikken.</p>","timeToRead":5,"excerpt":"Tiåret fra 1875 til 1885 var som sagt år med sterke brytninger i Griegs liv. Men selv om han ikke var særlig produktiv, skapte han verk som…","id":"/Users/thomasheimstad/Sites/Obo/src/posts/opus27.md absPath of file >>> MarkdownRemark","frontmatter":{"workname":"Strykekvartett i g-moll","composedin":"1878","startDate":"16.6","startTime":null,"location":"Troldhaugen","artists":["Engegårdkvartetten"],"medialink":null,"title":"27","dedicatedTo":"Robert Heckmann","author":"Erling Dahl jr.","date":"2018-05-07T12:00:00.048Z","category":"opus","tags":["strykekvartett"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":2.0963325183374084,"src":"/static/opus27-f9472a4b7ffbbb6006a63329b7f09b67-47774.jpg","srcSet":"/static/opus27-f9472a4b7ffbbb6006a63329b7f09b67-45ad6.jpg 480w,\n/static/opus27-f9472a4b7ffbbb6006a63329b7f09b67-e2fcf.jpg 960w,\n/static/opus27-f9472a4b7ffbbb6006a63329b7f09b67-47774.jpg 1920w,\n/static/opus27-f9472a4b7ffbbb6006a63329b7f09b67-93088.jpg 2880w,\n/static/opus27-f9472a4b7ffbbb6006a63329b7f09b67-bfc0d.jpg 3840w,\n/static/opus27-f9472a4b7ffbbb6006a63329b7f09b67-d62f9.jpg 4287w","sizes":"(max-width: 1920px) 100vw, 1920px"}}}},"fields":{"slug":"/opus27/","artist":[{"frontmatter":{"title":"Engegårdkvartetten","category":"artister","tags":["kvartett"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1,"src":"/static/EngegardKvartetten-0170353dd511c8731dca390d3682e129-466fa.jpg","srcSet":"/static/EngegardKvartetten-0170353dd511c8731dca390d3682e129-27b23.jpg 90w,\n/static/EngegardKvartetten-0170353dd511c8731dca390d3682e129-0afbd.jpg 180w,\n/static/EngegardKvartetten-0170353dd511c8731dca390d3682e129-466fa.jpg 360w,\n/static/EngegardKvartetten-0170353dd511c8731dca390d3682e129-43088.jpg 540w,\n/static/EngegardKvartetten-0170353dd511c8731dca390d3682e129-545bd.jpg 720w,\n/static/EngegardKvartetten-0170353dd511c8731dca390d3682e129-8c1c4.jpg 1080w,\n/static/EngegardKvartetten-0170353dd511c8731dca390d3682e129-89a6c.jpg 3840w","sizes":"(max-width: 360px) 100vw, 360px"}}}},"fields":{"slug":"/artister/engegardkvartetten/","opuses":[{"frontmatter":{"title":"27"}}]}}],"opuses":null}}},"pathContext":{"slug":"/opus27/"}}

